import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import button from '../../styles/buttons.scss'
import style from '../../styles/Contacts.scss'
import ToolBoxOptions from './ToolBoxOptions'
import _ from 'lodash'
import { Link } from 'react-router-dom'

import Page from './Page'
import Loader from './Loader'
import ContactList from './ContactList'

import Modal from '../modals/Modal'
import ContactsModal from '../modals/ContactsModal'
import ReactTooltip from 'react-tooltip'

import { VIEW_LIST, STATUS_LOAD, STATUS_DONE } from '../../constants'

export default class Contacts extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            current: null,
            modalOpen: false,
            edition: false
        }

        this.idDetected = false
    }

    componentWillMount () {
        this.props.onLoadContacts()
    }

    componentWillReceiveProps (newProps) {
        if(newProps.elements.length !== newProps.count) return newProps.onLoadContacts()

        if(this.props.match.params.id && !this.idDetected && newProps.elements.length > 0) {
            //Compute id
            this.idDetected = true
            for(const e of newProps.elements) {
                if(e._id === this.props.match.params.id) {
                    this.setState({
                        current: e,
                        modalOpen: true
                    })
                    break
                }
            }
        }
    }

    add() {
        this.setState({
            current: null,
            modalOpen: true,
            edition: true
        })
    }

    open(item) {
        this.setState({
            current: item,
            modalOpen: true
        })
    }

    select(item) {
        this.props.onSelectContact(item)
    }

    remove() {
        if(!this.state.current) return
        this.props.onRemoveContacts([this.state.current])
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    addOrSaveContact(contact) {
        if(this.state.current) {
            this.props.onSaveContact(this.state.current._id, contact)
        } else {
            this.props.onNewContact(contact.firstName, contact)
        }
        this.setState({
            modalOpen: false,
            current: null
        })
    }

  profilePreview() {
    cozy.client.fetchJSON('POST', '/permissions?codes=partage', {
      data: {
        type: 'io.cozy.permissions',
        attributes: {
          permissions: {
            "settings": {
              "description": "Required by the cozy-bar display Claudy and to know which applications are coming soon",
              "type": "io.cozy.settings",
              "verbs": ["ALL"]
            },
            "data-funerals-lastwill": {
              "description": "App required data Last Will access",
              "type": "com.empreinte.FLastWill",
              "verbs": ["ALL"]
            },
            "data-funerals-contacts": {
              "description": "App required data Last Will access",
              "type": "com.empreinte.Fcontacts",
              "verbs": ["ALL"]
            },
            "data-funerals-custom-contacts": {
              "description": "App required data Last Will access",
              "type": "com.empreinte.Fcustomcontacts",
              "verbs": ["ALL"]
            },
            "data-homeData": {
              "description": "App required data homeData access",
              "type": "com.empreinte.homeData",
              "verbs": ["ALL"]
            },
            "contacts": {
              "description": "App required contacts access",
              "type": "com.empreinte.contacts",
              "verbs": ["ALL"]
            },
            "metas": {
              "description": "App required metas access",
              "type": "com.empreinte.meta",
              "verbs": ["ALL"]
            },
            "contact": {
              "description": "App required contact access",
              "type": "io.cozy.contacts",
              "verbs": ["ALL"]
            }
          }
        }
      }
    }).then(result => {
      this.sharing = result.attributes.codes['partage'];
      console.log(this.sharing);

      window.open('/public?sharecode=' + this.sharing + '#/','_blank');
    })
  }

    render() {
        cozy.client.data.findAll("com.empreinte.contacts")
        .then((index) => {
          console.log(index)
        })
        const { elements, count, selectedCount, status } = this.props

        const loading = status === STATUS_LOAD

        const add = (<button onClick={() => this.add()} className={classNames(button.button, button.default)}><img className={style.add}  src="media/add.svg" />AJOUTER UN RFRENT</button>)
        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.props.onSelectAll()
                    }}>
                        <i className="ion-ios-checkmark-outline">TOUT SELECTIONNER</i>
                    </li>

                </ul>
            </ToolBoxOptions>
        )
        const remove = (<button onClick={() => this.props.onRemoveContacts(elements.filter(f => f.selected))} className={classNames(button.button, button.assertive)}><img className={style.add}  src="media/add.svg" />SUPPRIMER</button>)

        const actions = [add]
        if(selectedCount > 0)
                actions.unshift(remove)

        let list = undefined
        if(!loading && elements.length > 0) {
            const sort = elements.sort((a, b) => {
                if(a.lastName < b.lastName) return -1
                if(a.lastName > b.lastName) return 1
                return 0
            })

            const sortedContacts = _.groupBy(sort, item => {
                let name = item.lastName || item.firstName || item.company || '_'
                name = name.trim().toUpperCase()

                const firstLetter = name[0]

                if ((/[0-9]/g).test(firstLetter)) {
                    return '#'
                }
                return firstLetter
            })

            const keys = Object.keys(sortedContacts)
            const sortedKeys = keys.sort((a,b) => {
                if(a < b) return -1
                if(a > b) return 1
                return 0
            })

            list = []

          console.log(elements);

            for(let user of elements) {
              console.log(user);
                list.push(
                    <div>

                            {user.firstname}

                    </div>
                )
            }
            console.log(list);
            const sharing = cozy.client.files.getCollectionShareLink("dd9d4d75f37d781adf3b0c2b5c3434a2", 'com.empreinte.homeData')
            console.log(sharing)
        }

        return (
            <Page title="Mes proches" subtitle="Titulaire du compte ou proche dsign par celui-ci, vous avez accs aux mmes informations, mais pas en mme temps. Le premier remplit le dossier selon ses choix intimes, renseigne les contacts, dsigne les intervenants... Quelque part, il met en scne son dpart. Quant au second, il suit les indications, droule le scnario sereinement et accompagne lapplication des dcisions. Un lien fort se concrtise dans cet Espace priv.">
              <Loader display={loading}/>
              <div className={style.row}>
                <h3 className={style.titlePage} >Mes proches rfrents</h3>
                    <ul className={style.actions}>
                    { actions && actions.map((item, key) => (<li key={key}>{item}</li>)) }
                    </ul>
              </div>
            <div className={style.row} >
                <p className={style.introProches}  >
                    Le titulaire du compte dsigne un ou plusieurs de ses proches qui auront accs aux donnes aprs le dcs. Ils les utiliseront pour organiser les obsques selon les choix et pourront s'acquitter aisment de l'ensemble des dmarches administratives. Le titulaire dsigne cette ou ces personne(s) de confiance ici.
                    <br />
                    En crant un proche rfrent, le proche reoit un mail l'informant de la cration de l'espace dcs du titulaire et de son adhsion au contrat obsque de la MAIF. il y est notifi qu'en cas de dcs, le proche rfrent doit prvenir Parnasse-MAIF au 05 49 73 89 43. Celle-ci met en place les garanties et prestations du contrat obsques ds qu'elle en est informe.
                  <br /><br />
                  Pour autoriser l'accs du proche  son espace dcs, le titulaire choisit entre deux possibilits : <br />
                   - <b>De son vivant</b>. Dans ce cas, il coche dans la fiche "Ajouter un rfrent" la mention "Partage immdiat". Le mail contenant le lien est automatiquement envoy au proche. <br />
                   - <b>Au moment du dcs</b>. Dans ce cas, il coche dans la fiche "Ajouter un rfrent" la mention "Partage diffr". Ds que Parnasse-MAIF est prvenue par le proche du dcs du titulaire, elle lui envoie le lien vers l'espace dcs.

                </p>
            </div>
            <div className={style.dataReferent}  >
              {
                elements.map((item) => {
                  if (item.statut === "ref")
                    if (item.delay === "now")
                      return (<span className={style.proche}> {item.firstName} {item.lastName} <img className={style.chevron}
                                                                           src="public/media/chevronpurple.svg"/> &nbsp;&nbsp;Partage immdiat <br/> </span>)
                    else
                      return (<span className={style.proche}> {item.firstName} {item.lastName} <img className={style.chevron}
                                                                           src="public/media/chevronpurple.svg"/> &nbsp;&nbsp;Partage diffr <br/> </span>)
                })
              }
            </div>
              <br /><br /><br />

              <div className={style.row}>
                <h3 className={style.titlePage} >Mes autres proches  prvenir </h3>
                    <ul className={style.actions}>
                    { actions && actions.map((item, key) => (<li key={key}>{item}</li>)) }
                    </ul>
              </div>
            <div className={style.row} >
                <p className={style.introProches}  >
                    Le titulaire entre ici la liste des personnes qui seront prvenues de son dcs. Notamment celles dont les coordonnes ne sont peut-tre pas connues des proches rfrents (voisin, ami denfance, femme de mnage ).
                </p>
            </div>
            <div className={style.dataPrevenir}  >
              {
                elements.map((item) => {
                  if (item.statut === "prev")
                      return (<span className={style.proche}> {item.firstName} {item.lastName} <br/> </span>)
                })
              }
            </div>

              <hr className={style.seperator}/>
                <div className={style.row}>
                  <h3 className={style.titlePage} data-type="light" data-tip="Ce dossier sadresse aux proches rfrents. Il contient les dernires volonts du titulaire et un assistant administratif qui hirarchise et automatise les dmarches. " >Mon dossier obsques accessible par mes proches<img src="media/information.svg" className={style.information} /></h3>
                </div>
                <div className={style.row} >
                  <p className={style.introProches}  >
                    Les proches rfrents accdent au dossier obsques du titulaire. Il contient les dernires volonts de la personne ainsi qu'un assistant administratif qui hirarchise et automatise les dmarches.
                  </p>
                </div>
                <div className={style.dataObseque}  >
                  <span onClick={() => this.profilePreview()}>
                    <div className={style.divContact} >
                      <div className={style.contentContact}  >
                        <p className={style.contactName} >Mon dossier obsque</p>
                        <img  className={style.chevron} src="media/chevronpurple.svg" />
                      </div>
                      <hr className={style.seperator}/>
                    </div>
                  </span>

                </div>
              <ReactTooltip />
              {/* {!loading && elements.length === 0 && "Aucun contact"} */}

              <Portal isOpened={this.state.modalOpen} closeOnEsc onClose={() => this.setState({modalOpen: false, current: null, edition: false})}>
                <Modal>
                  <ContactsModal
                    item={this.state.current}
                    title={this.state.current ? "Modifier votre contact" : "Crer votre contact"}
                    onSave={contact => this.addOrSaveContact(contact)}
                    onRemove={() => this.remove()}
                    edit={this.state.edition}/>
                </Modal>
              </Portal>

            </Page>
        )
    }
}



