import React, {Component} from 'react'
import classNames from 'classnames'

import contactTypes from '../../constants/data/contactTypes.json'
import {createOptionsFromArray, getDataElements} from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/ContactRecallModal.scss'

export default class ContactModal extends Component {
    constructor(props, context) {
        super(props, context)

        const item = props.item || {}

        this.state = {
          import: true,
          lastName: item.civility || '0',
            lastName: item.lastName || '',
            firstName: item.firstName || '',
            company: item.company || '',
            type: item.type || '',
            email: item.email || '',
            telephones: item.telephones || [{
              phone: '',
              type: 'fixe'
            }],
            address: item.address || {
                way: '',
                code: '',
                city: ''
              },
            birth: item.birth || {
              day: '',
              month: '',
              year: ''
            },
            commentary: item.commentary || '',
            statut: item.statut || 'prev',
            delay: item.delay || 'now',
            edit: false,
            warn: false
        }

    }

    componentWillMount() {
        if (this.props.edit === true)
            this.state.edit = true
    }

    componentWillReceiveProps(newProps) {}

    onimport(value = false){
      this.setState({import: value})
    }

    save(e) {
        //create new object
        const {
            civility,
            lastName,
            firstName,
            company,
            type,
            email,
            statut,
            delay,
            commentary
        } = this.state

        if(!lastName && !company) return this.setState({warn: true})

        const telephones = [...this.state.telephones]
        const address = {
            ...this.state.address
        }

        const newContact = {
            civility,
            lastName,
            firstName,
            company,
            type,
            email,
            commentary,
            telephones,
            address,
            statut,
            delay
        }

        let infos = null;

      cozy.client.settings.getInstance().then(result => {
        console.log(result);
        infos = result;
      }).catch(error => {
        console.log(error);
      })


      getDataElements('com.empreinte.homeData')
        .then(res => {
          this.user = res[0];
          console.log(this.user);


          cozy.client.fetchJSON('POST', '/permissions?codes=partage', {
            data: {
              type: 'io.cozy.permissions',
              attributes: {
                permissions: {
                  "settings": {
                    "description": "Required by the cozy-bar display Claudy and to know which applications are coming soon",
                    "type": "io.cozy.settings",
                    "verbs": ["ALL"]
                  },
                  "data-funerals-lastwill": {
                    "description": "App required data Last Will access",
                    "type": "com.empreinte.FLastWill",
                    "verbs": ["ALL"]
                  },
                  "data-funerals-contacts": {
                    "description": "App required data Last Will access",
                    "type": "com.empreinte.Fcontacts",
                    "verbs": ["ALL"]
                  },
                  "data-funerals-custom-contacts": {
                    "description": "App required data Last Will access",
                    "type": "com.empreinte.Fcustomcontacts",
                    "verbs": ["ALL"]
                  },
                  "data-homeData": {
                    "description": "App required data homeData access",
                    "type": "com.empreinte.homeData",
                    "verbs": ["ALL"]
                  },
                  "contacts": {
                    "description": "App required contacts access",
                    "type": "com.empreinte.contacts",
                    "verbs": ["ALL"]
                  },
                  "metas": {
                    "description": "App required metas access",
                    "type": "com.empreinte.meta",
                    "verbs": ["ALL"]
                  },
                  "contact": {
                    "description": "App required contact access",
                    "type": "io.cozy.contacts",
                    "verbs": ["ALL"]
                  }
                }
              }
            }
          }).then(result => {
            this.sharing = result.attributes.codes['partage'];
            console.log(this.sharing);

            let url = 'http://' + window.location.hostname + '/public?sharecode=' + this.sharing + '#/';
            let civ = newContact.civility === 0 ? "Madame" : "Monsieur";

            let now = "Bonjour " + civ + " " + newContact.lastName + ". Vous recevez ce mail dinformation car" + this.user.civilite + " " + this.user.name + " vous a dsign comme proche rfrent dans lespace <b><i>Faire face au dcs</b></i> de la MAIF. Nous vous en flicitons.<br />Votre proche souhaite anticiper ses obsques et prparer les dmarches qui en dcouleront. Il a souscrit le contrat obsques SOLLICITUDES de la MAIF et a ouvert un espace priv.<br /><br />Au moment de son dcs, nous vous demandons de contacter immdiatement Parnasse-MAIF au:<br /><center><b>05 49 73 89 43</b></center><br />Celle-ci mettra en place lensemble des garanties et des prestations du contrat obsques. Loffre souscrite facilitera considrablement votre cheminement dans ce moment douloureux. Lobjectif est dallger la situation, de lapaiser au maximum. <br />Lespace <b><i>Faire face au dcs</b></i> de votre proche contient des informations qui vous seront prcieuses pour lorganisation de ses obsques: dernires volonts, don de soi, prestataire funraire  ainsi que les informations et contacts administratifs auxquels il est rattach. Vous naurez pas  les chercher, tout est ordonn. Vous aurez accs  un assistant administratif. Il vous facilitera la tche en vous guidant pas  pas dans vos dmarches. <br /><br />Vous pouvez dores et dj accder  certaines informations du dossier en cliquant sur ce lien: " + url + "<br />Au nom de notre socitaire, nous vous remercions vivement pour votre implication. <br /><br />Cordialement <br /><br />Lquipe Parnasse-MAIF"

            let diff = "Bonjour " + civ + " " + newContact.lastName + ". Vous recevez ce mail dinformation car" + this.user.civilite + " " + this.user.name + " vous a dsign comme proche rfrent dans lespace <b><i>Faire face au dcs</b></i> de la MAIF. Nous vous en flicitons.<br />Votre proche souhaite anticiper ses obsques et prparer les dmarches qui en dcouleront. Il a souscrit le contrat obsques SOLLICITUDES de la MAIF et a ouvert un espace priv.<br /><br />Au moment de son dcs, nous vous demandons de contacter immdiatement Parnasse-MAIF au:<br /><center><b>05 49 73 89 43</b></center><br />Celle-ci mettra en place lensemble des garanties et des prestations du contrat obsques. Loffre souscrite facilitera considrablement votre cheminement dans ce moment douloureux. Lobjectif est dallger la situation, de lapaiser au maximum. <br />Lespace <b><i>Faire face au dcs</b></i> de votre proche contient des informations qui vous seront prcieuses pour lorganisation de ses obsques: dernires volonts, don de soi, prestataire funraire  ainsi que les informations et contacts administratifs auxquels il est rattach. Vous naurez pas  les chercher, tout est ordonn. Vous aurez accs  un assistant administratif. Il vous facilitera la tche en vous guidant pas  pas dans vos dmarches.<br /><br />Lors de la dclaration du dcs, Parnasse-MAIF vous adressera un mail contenant le lien vers lespace de votre proche.<br />Au nom de notre socitaire, nous vous remercions vivement pour votre implication. <br /><br />Cordialement <br /><br />Lquipe Parnasse-MAIF"

            let body = delay === "now" ? now : diff;

            const job = cozy.client.jobs.create('sendmail', {
              mode: 'from',
              to: [
                {name: 'COZY - MAIF OBSEQUE', email: newContact.email}
              ],
              subject: this.user.firstname + " " + this.user.name + ' vous donne accs  ses informations.',
              parts: [
                {type: 'text/html', body: body}
              ]
            }).then(res => {
              console.log(res);
            })
          })

          console.log(newContact);
          this.props.onSave(newContact)
          this.close(e)

        })



    }

    close(e) {
        e.stopPropagation()

        //close the portal
        this
            .props
            .closePortal()
    }

    edit(e) {
        e.stopPropagation()

        this.setState({edit: true})
    }

    remove(e) {
        this
            .props
            .onRemove()
        this.close(e)
    }

    addTelephone(e) {
        e.preventDefault()
        e.stopPropagation()
        this.setState({
            telephones: [
                ...this.state.telephones, {
                    phone: '',
                    type: 'fixe'
                }
            ]
        })
    }

    renderEdition() {
        return (
            <div className={style.edition} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.props.title}</h1>
                </div>
              {this.state.import == false &&  <form className={style.content}>
                    <div className={style.row}>
                      <div className={style.group}>
                        <label>Civilit</label>
                          <select
                            type="text"
                            placeholder="Civilit"
                            value={this.state.civility}
                            onChange={(e) => this.setState({civility: e.target.value})} >
                            <option value={0}>Madame</option>
                            <option value={1}>Monsieur</option>
                            <option value={2}>Autre</option>
                          </select>
                      </div>

                        <div className={style.group}>
                            <label>Nom</label>
                            <input
                                type="text"
                                placeholder="Nom"
                                value={this.state.lastName}
                                style={ifStyle(this.state.warn && this.state.lastName === '', {border: '1px solid orangered'})}
                                onChange={e => this.setState({lastName: e.target.value})} />
                        </div>

                        <div className={style.group}>
                            <label>Prnom</label>
                            <input
                                type="text"
                                style={ifStyle(this.state.warn && this.state.firstName === '', {border: '1px solid orangered'})}
                                placeholder="Prnom"
                                value={this.state.firstName}
                                onChange={e => this.setState({firstName: e.target.value})} />
                        </div>
                    </div>


                    <div className={style.row}>
                      <div className={style.group}>
                      <label>Adresse</label>
                        <input
                            type="text"
                            placeholder="Adresse"
                            value={this.state.address.way}
                            onChange={e => this.setState({
                            address: {
                                ...this.state.address,
                                way: e.target.value
                            }
                        })} />
                      </div>
                    </div>
                <div className={style.row}>
                  <div className={style.group}>
                    <input
                        type="text"
                        placeholder="Code postal"
                        value={this.state.address.code}
                        onChange={e => this.setState({
                        address: {
                            ...this.state.address,
                            code: e.target.value
                        }
                    })} />
                  </div>
                  <div className={style.group}>
                    <input
                        type="text"
                        placeholder="Ville"
                        value={this.state.address.city}
                        onChange={e => this.setState({
                        address: {
                            ...this.state.address,
                            city: e.target.value
                        }
                    })} />
                  </div>
                  <div className={style.group}>
                    <input
                      type="text"
                      placeholder="Pays"
                      value={this.state.address.land}
                      onChange={e => this.setState({
                      address: {
                          ...this.state.address,
                          land: e.target.value
                      }
                    })} />
                  </div>
                </div>

                <div className={style.row}>

                    <div className={style.group}>
                      <label>Email (OBLIGATOIRE)</label>
                      <input
                        type="email"
                        placeholder="Email"
                        value={this.state.email}
                        onChange={e => this.setState({email: e.target.value})} />
                    </div>

                  <div className={style.group}>
                    <label>Tlphone</label>
                    {this.state.telephones && this
                      .state
                      .telephones
                      .map((item, key) => (
                        <div key={key} className={style.inline}>
                          <input
                            type="text"
                            placeholder="Tlphone"
                            value={item.phone}
                            onChange={e => {
                              item.phone = e.target.value;
                              this.setState({});
                            }} />
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className={style.group}>
                    <label>Accs  l'espace dcs</label>
                    <select
                      placeholder="Accs  l'espace dcs"
                      value={this.state.delay}
                      onChange={e => this.setState({delay: e.target.value})}
                    >
                      <option value="now">Partage immdiat</option>
                      <option value="death">Partage diffr</option>
                    </select>
                  </div>


              <div className={style.row}>
                <div className={style.group}>
                <label>Statut du proche</label>
                <select
                placeholder="Proche  prvenir ou proche rfrent"
                value={this.state.statut}
                onChange={e => this.setState({statut: e.target.value})}
                >
                <option value="prev">Proche  prvenir</option>
                <option value="ref">Proche rfrent</option>
                </select>
                </div>

              </div>

                </form> }
              {this.state.import == true &&
              <div>
                <div className={classNames("col-md-6", style.importCard)} onClick={() => this.onimport()}>
                  <i className={"ion-ios-person"}></i> <br/>

                  Crer votre contact <br/>
                  <div>
                    Plus tard, il sera possible d'importer vos contacts depuis votre espace MAIF
                  </div>

                </div>
                <div className={classNames("col-md-6", style.importCard, style.disable)}>
                  <div className={style.overlay}>

                  </div>
                  <i className={classNames( "ion-ios-people")}></i> <br/>

                  Importer votre contact <br/>

                </div>
              </div>
              }
                <div className={style.footer}>
                  <div className={style.footerRight}>
                    <button
                      onClick={e => this.close(e)}
                      className={classNames(button.button, button.defaultLight, style.button)}
                    >
                      FERMER
                    </button>

                    {
                      this.state.import == false && <button
                        className={classNames(button.button, button.default, style.button)}
                        onClick={(e) => this.save(e)}>
                        VALIDER
                      </button>
                    }
                  </div>
                </div>
            </div>
        )
    }

    renderView() {
        return (
            <div className={style.main} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.state.civility} {this.state.firstName} {this.state.lastName && this
                            .state
                            .lastName
                            .toUpperCase()}</h1>
                </div>
                <div className={style.content}>
                  <div>
                    <h3>Anniversaire</h3>
                    <p>{this.state.birth.day} / {this.state.birth.month} / {this.state.birth.year}</p>
                  </div>
                    <div>
                        <h3>Tlphone</h3>
                        {this.state.telephones && this
                            .state
                            .telephones
                            .map((item, index) => (
                                <p key={index}>
                                    <strong>{item.type && item
                                            .type
                                            .toUpperCase() + ' :'}</strong>
                                    <a href={"tel:" + item.phone}>{item.phone}</a>
                                </p>
                            ))}
                    </div>

                    <div>
                        <h3>Email</h3>
                        <p>
                            <a href={"mailto:" + this.state.email}>{this.state.email}</a>
                        </p>
                    </div>

                    <div>
                        <h3>Adresse</h3>
                        <p>{this.state.address.way}</p>
                        <p>{this.state.address.code}</p>
                        <p>{this.state.address.city}</p>
                        <p>{this.state.address.land}</p>
                    </div>
                </div>
              <div className={style.footer}>
                <div className={style.footerRight}>
                  <button
                    className={classNames(button.button, button.default, style.button)}
                    onClick={(e) => this.edit(e)}>
                    <span className={button.text}>MODIFIER</span>
                  </button>

                  <button
                    onClick={e => this.remove(e)}
                    className={classNames(button.button, button.assertive, style.button)}>
                    <span className={button.text}>SUPPRIMER</span>
                  </button>

                  <button
                    onClick={e => this.close(e)}
                    className={classNames(button.button, button.default, style.button)}
                  >
                    ANNULER
                  </button>
                </div>
              </div>
            </div>
        )
    }

    render() {
        if (this.state.edit)
            return this.renderEdition()
        else
            return this.renderView()
    }

}

const ifStyle = (condition, style) => {
    if(condition) return style
    return {}
}



