import React from "react";
import classNames from 'classnames'

import Page from './Page'

import style from '../../styles/FuneralList.scss'
import stylePresta from '../../styles/Prestataire.scss'
import styleLoca from '../../styles/Localisation.scss'


const Data = props => {
  console.log(props.open_hours)
  return (
    <div>
      <div className={styleLoca.row} >
        <div className={styleLoca.name}>
          <div>{props.name}</div>
        </div>
      </div>
      <div className={classNames(style.row)}>
        <div>
          <div className={styleLoca.dataAdress}  >{props.formatted_address}</div>
          <div className={styleLoca.dataOpening} > {props.open_hours == true ? "Ouvert maintenant" : "Ferm"} </div>
        </div>
      </div>
      <div className={style.separator}></div>
    </div>
  )
}

export default class Localisation extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      open: false,
      current: '',
      hiddenType: '',
      search: '',
      typeGroup: [],
      customTypeGroup: [],
      TypeOpen: false,
      InfoOpen: false,
      infoHeader: "",
      infoText: "",
      firstname: "",
      formType: "",
      newTypeType: "",
      lastname: "",
      link: '',
      zoom: 11,
      currentMap: {},
      dataAdress: {}
    };
    this.initMap = this.initMap.bind(this)
    this.searchBox = this.searchBox.bind(this)
  }

  loadJS(src) {
    console.log(src);
    var ref = window.document.getElementsByTagName("script")[0];
    console.log(ref);
    var script = window.document.createElement("script");
    script.src = src;
    script.async = true;
    console.log(script);
  //  ref.parentNode.insertBefore(script, ref);
    document.body.insertBefore(script, document.body.lastChild);
  }

  componentDidMount() {
    window.initMap = this.initMap;
    window.loadJS = this.loadJS;
    window.searchBox = this.searchBox
    //loadJS('https://maps.googleapis.com/maps/api/js?key=AIzaSyCu4Xg-71dwXAhpvwe3Vhqqj51NmWkmQtU&callback=initMap&libraries=places')
    console.log("MMMMDDDDDRRRR");
    loadJS('https://maps.googleapis.com/maps/api/js?key=AIzaSyCu4Xg-71dwXAhpvwe3Vhqqj51NmWkmQtU&callback=initMap&libraries=places')
  }

  initMap() {
    var map = new google.maps.Map(document.getElementById('map'), {
      center: { lat: -34.397, lng: 150.644 },
      zoom: 11
    });
    this.setState({ currentMap: map })
    var infoWindow = new google.maps.InfoWindow;

    // Try HTML5 geolocation.
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function (position) {
        var pos = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };

        infoWindow.setPosition(pos);
        infoWindow.setContent('Location found.');
        infoWindow.open(map);
        map.setCenter(pos);
        searchBox(map);
      }, function () {
        //   handleLocationError(true, infoWindow, map.getCenter());
      });
    } else {
      // Browser doesn't support Geolocation
      // handleLocationError(false, infoWindow, map.getCenter());
    }
  }

  searchBox(map) {
    map = this.state.currentMap;
    var input = document.getElementById('input');
    var searchBox = new google.maps.places.SearchBox(input);
    // map.controls[google.maps.ControlPosition.TOP_LEFT].push(input);
    map.addListener('bounds_changed', function () {
      searchBox.setBounds(map.getBounds());
    });
    var markers = [];
    searchBox.addListener('places_changed', () => {
      var places = searchBox.getPlaces();
      console.log(places)
      this.setState({ dataAdress: places })
      if (places.length == 0) {
        return;
      }

      markers.forEach(function (marker) {
        marker.setMap(null);
      });
      markers = [];

      var bounds = new google.maps.LatLngBounds();
      places.forEach(function (place) {
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }
        var icon = {
          url: "/media/marqueur.png",
          size: new google.maps.Size(71, 71),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(17, 34),
          scaledSize: new google.maps.Size(25, 25)
        };

        markers.push(new google.maps.Marker({
          map: map,
          icon: icon,
          title: place.name,
          position: place.geometry.location
        }));

        if (place.geometry.viewport) {
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
      });
      map.fitBounds(bounds);
    });
  }


  render() {
    const allData = this.state.dataAdress;
    return (
      <Page title="Prparer mes obsques" subtitle="Cet espace vous permet de renseigner l'ensemble des informations qui seront utiles  vos proches. ">
        <div className={style.main}>
          <div className={style.header}>
            <div className={stylePresta.titlePresta} >
              <h3>Go-localisation de votre prestataire funraire</h3>
            </div>
            <div className={styleLoca.contentMap} >
              <div className={styleLoca.right}  >
                <div className={styleLoca.map} id="map" className={styleLoca.map} ></div>
              </div>
              <div >
                <input onBlur={this.searchBox} id="input" defaultValue="pompes funebres generales (pfg)" className={styleLoca.controls} type="text" />
                <div className={styleLoca.printedAddress} >
                  {
                    Object.keys(allData).length === 0 && allData.constructor === Object ? (
                      <div></div>
                    ):(
                      this.state.dataAdress.map((item, key) => {
                        if (item.hasOwnProperty('opening_hours')) {
                        console.log(item.opening_hours.open_now)
                        return (
                          <Data name={item.name} formatted_address={item.formatted_address} open_hours={item.opening_hours.open_now} />
                        )
                        }
                      })
                    )
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      </Page>
    );
  }
}



