export const getMeta = async() => {
    try {
        let indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name']);
        let result = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "onboarding"
            }
        })

        return result
    } catch (error) {
        return []
    }
}

export const getContactMeta = async() => {
    try {
        let indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name']);
        let result = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "contactType"
            }
        })

        return result
    } catch (error) {
        return []
    }
}

export const getCustomContactMeta = async() => {
    try {
        let indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name']);
        let result = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "customContactType"
            }
        })

        return result
    } catch (error) {
        return []
    }
}

export const isAlreadyShown = async() => {

    const res = await getMeta();
    const contacts = await getContactMeta()
    const customContacts = await getCustomContactMeta()

    console.log(res);
    console.log(contacts);

    if(contacts.length > 0){
      // if(contacts[0].value[0].types.length < typeContact.types.length){
        await cozy.client.data.delete(DOCTYPE_META, contacts[0])
        await cozy.client.data.create(DOCTYPE_META, {name: "contactType", value: [typeContact]})
      // }
    }
    console.log(customContacts)
    if(customContacts.length === 0){
      await cozy.client.data.create(DOCTYPE_META, {name: "customContactType", value: [ {
          "name": "Mes contacts custom",
          "location": "/Contacts custom",
          "types": []
        }]})
    }

    if(res.length === 0){
        return false
    } else if(res.length === 1) {
        return res[0].value
    } else {
        const value = res.shift()
        //remove all others info
        for(const e of res){
            await cozy.client.data.delete(DOCTYPE_META, e)
        }
        return value
    }
}

export const markAsShown = async(shown = true) => {
    const res = await getMeta()

    console.log("salut");

    for(const e of res){
        await cozy.client.data.delete(DOCTYPE_META, e)
    }
    await cozy.client.data.create(DOCTYPE_META, {name: "onboarding", value: shown})

    const contacts = await getContactMeta()
    await cozy.client.data.create(DOCTYPE_META, {name: "contactType", value: [typeContact]})
}



