import React from "react";
import { Router } from 'react-router'
import { Route, IndexRoute, Switch, Redirect} from 'react-router-dom'
import { createHashHistory } from 'history'
import Home from "./containers/Home";
import Navigation from "./containers/Navigation";
import Contacts from './containers/Contacts'
import Preferences from './containers/Preferences'
import FuneralList from './containers/FuneralList'
import DataForm from './containers/DataForm'
import Data from './containers/Data'
import style from './styles/App.scss'
import { shouldEnableTracking, getTracker } from './tracker'
import ROOT_PATH from './constants/index'
import AssistanceList from "./components/AssistanceList";
import Index from "./components/elements/index";
import Footer from "./components/elements/Footer";
import Prestataire from "./components/elements/Prestataire";
import Localisation from "./components/elements/Localisation";
import Devis from './containers/Devis';
import Dossier from './containers/Dossier';
import DossierSend from './public/containers/Home';
import Don from './containers/Don';
import DonDevis from './containers/DonDevis';
import Retraite from './containers/Retraite';
import Complementaire from './containers/Complementaire';
import Banque from './containers/Banque';
import Mairie from './containers/Mairie';
import Employeur from './containers/Employeur';
import Medecin from './containers/Medecin';
import Credit from './containers/Credit';
import Association from './containers/Association';
import SecuriteSociale from './containers/SecuriteSociale';
import Assurance from './containers/Assurance';
import Notaire from './containers/Notaire';
import Bailleur from './containers/Bailleur';
import CaisseAllocation from './containers/CaisseAllocation';
import Organisme from './containers/Organisme';
import Impot from './containers/Impot';
let history = createHashHistory();

history.listen(() => window.scrollTo(0, 0))


// app component
const App = ( props ) => {



  if (shouldEnableTracking() && getTracker()) {
    let trackerInstance = getTracker()
    history = trackerInstance.connectToHistory(history)
    trackerInstance.track(history.location.pathname) // when using a hash history, the initial visit is not tracked by piwik react router
  }

  cozy.client.files.statByPath(ROOT_PATH)
    .then(() => {
      props.initilizeDocs()
    })
    .catch(err => {
      console.log('No route file')
      props.initilizeDocs()
    })



  // render
  return (
    <Router history={history}>

      <div className={style.app}>
        <div className={style.container}>
          <Navigation />
          <div className={style.page}>
            <main className={style.main}>
              <Route path="/" component={Index}/>
              <Route path="/home" component={Home}/>
              <Route path="/relation" component={Contacts}/>
              <Route path="/funerals" component={FuneralList}/>
              <Route path="/assistances" component={AssistanceList}/>
              <Route path="/funerals/:slug" component={DataForm}/>
              <Route path="/data/:slug/:id" component={Data}/>
              <Route path="/preferences" component={Preferences}/>
              <Route path="/prestataire" component={Prestataire}/>
              <Route path="/localisation-funeraire" component={Localisation}/>
              <Route path="/devis" component={Devis}/>
              <Route path="/dossier" component={Dossier}/>
              <Route path="/don" component={Don}/>
              <Route path="/devis-don" component={DonDevis}/>
              <Route path="/retraite" component={Retraite}/>
              <Route path="/complementaire-maladie" component={Complementaire}/>
              <Route path="/banque" component={Banque}/>
              <Route path="/mairie" component={Mairie}/>
              <Route path="/employeur" component={Employeur}/>
              <Route path="/medecin" component={Medecin}/>
              <Route path="/credit" component={Credit}/>
              <Route path="/association" component={Association}/>
              <Route path="/securite-sociale" component={SecuriteSociale}/>
              <Route path="/assurance" component={Assurance}/>
              <Route path="/notaire" component={Notaire}/>
              <Route path="/bailleur" component={Bailleur}/>
              <Route path="/caf" component={CaisseAllocation}/>
              <Route path="/organisme" component={Organisme}/>
              <Route path="/impot" component={Impot}/>
              <Footer/>

            </main>
          </div>
        </div>
      </div>

    </Router>
  );
}
export default App



