import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from './Page'

import { Link } from 'react-router-dom'

import {getFileTypes, getTypesByGroup} from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import stylecontact from '../../styles/Contacts.scss'
import stylenav from '../../styles/Navigation.scss'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import {DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_F_YOUNES} from "../../constants/index";
import {getFields, getInfosFromDoctype} from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { markAsShown, isAlreadyShown } from '../../scripts/onboarding'
import {getMeta} from "../../scripts/meta";

import shareHome from '../../public/constants/data/shareHome.json';

const Doctypes = [
  {
    doctype: DOCTYPE_F_LASTWILL,
    display: 'Mes dernieres volonts',
    excerpt: 'Expliquez  vos proches vos attentes pour vos obsques',
    path: '/funerals/lastwill',
    slug: 'lastwill',
    internpath: false,
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Don du corps  la science',
    excerpt: "Toutes les informations pour donner son corps  la science ou pour stipuler son refus de don dorgane.",
    path: '/don',
    internpath: '/don',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Refus du don d\'organe ',
    excerpt: "Toutes les informations pour donner son corps  la science ou pour stipuler son refus de don dorgane.",
    path: '/don',
    internpath: '/don',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'Mes Proches Rfrents',
    excerpt: "Il s'agit des personnes qui auront accs  vos informations aprs votre dcs.",
    path: '/funerals/works',
    internpath: '/relation',
    unique: true
  },
  {
    doctype: DOCTYPE_CONTACTS,
    display: 'L\'employeur ou Ple Emploi ' ,
    excerpt: "Lorem ipsum dolor sit amet",
    path: '/funerals/works',
    internpath: '/relation',
    unique: true
  }
]

const Row = props => {
  return (
    <div onClick={e => props.onAdd && props.onAdd(e)}>
      <div className={classNames(style.row)}>
        <div className={style.name}>
          <div>{props.title} <img  className={stylenav.chevron} src="media/chevronpurple.svg" /></div>
          <div className={style.excerpt}>{props.excerpt}</div>
        </div>
      </div>
      <div className={style.separator}></div>
    </div>

  )
}
// FuneralList page component
export  default class Home extends React.Component {
  constructor(props) {
    super(props)

    this.fileTypes = getFileTypes()
    this.typeGroup = getTypesByGroup()

    this.state = {
      open: false,
      openOnboarding: false,
      openFirstOnboarding: false,
      current: '',
      InfoOpen: false,
      infoHeader: "",
      infoText: "",
      hiddenType: '',
      search: '',
      data: [],
      typeGroup: [],
    }

    getMeta()
      .then(result => {
        if (result.length === 0) {
          console.log("hihi");
          markAsShown(true).then((res) => { console.log(res) })
        }
        else {
          this.setTypeGroup(result[0].value)

          getDataElements(DOCTYPE_F_CONTACTS)
            .then(res => {
              if(res.length > 0) {
                let type = result[0].value;
                for(let n in type[0].types){
                  let exist = _.find(res, t => t.type === type[0].types[n].name)
                  if(exist){
                    type[0].types[n].checked = true;
                    type[0].types[n]._id = exist._id;
                  }
                }
                this.setTypeGroup(type)
              }
              console.log(res)
            })
        }
      });
  }

  setTypeGroup(typeGroup){
    this.setState({typeGroup: typeGroup})
    this.render()
  }

  componentWillMount(){
    this.computeWidth(this.props)
    this.steps = 1;

    isAlreadyShown()
      .then((res) => {
        console.log(res);
        if (res) {
          return this.closeFirstOnboarding(true)
        }

        return markAsShown(true)
          .then(() => {
            this.openFirstOnboarding()
          })
      })
  }

  componentWillReceiveProps(newProps) {
    this.computeWidth(newProps)
  }

  computeWidth(props) {
    this.width = 0
    const notPersoFileTypes = this.fileTypes.filter(f => !f.perso)
    const notPersoUsed = props.usedType.filter(elem => _.find(notPersoFileTypes, f => f.key === elem.type) === undefined)
    const totalUsed = notPersoUsed.length + props.dataCreated.length
    const totalTypes = notPersoFileTypes.length + Doctypes.length
    this.width = (totalUsed / totalTypes) * 100
  }

  clickDocument(key, location) {
    this.props.onFileClick(location, key)
    this.setState({open: true})
  }
  clickOnWill(data) {
    const item = getInfosFromDoctype(data.doctype);
    this.setState({current:item.slug, open: true});
  }
  closeOnboarding(){
    this.setState({openOnboarding: false});
    this.forceUpdate()
  }
  openOnboarding(){
    this.setState({openOnboarding: true});
  }
  openFirstOnboarding(){
    this.setState({openFirstOnboarding: true});
  }

  closeFirstOnboarding(init = false){
    if(init){
      this.setState({openFirstOnboarding: false});
    }
    else{
      this.setState({openFirstOnboarding: false, openOnboarding: true});
    }
  }

  clickOnContacts(data = '') {
    console.log(data)
    const item = getInfosFromDoctype(DOCTYPE_F_CONTACTS);
    this.setState({current:item.slug, formType: data.type, open: true, hiddenType: data.name});
  }

  clickOninfo(e, header = '', text = '') {
    e.preventDefault()
    e.stopPropagation()
    this.setState({InfoOpen: true, infoText: text, infoHeader: header});
  }
  // render
  render() {
 return (
      <Page title="Votre espace priv dcs"
            subtitle="Bienvenue dans votre espace priv.
            Quel que soit son ge, on a tous dans l'ide de laisser nos affaires en ordre,
            au cas o... Ici, vous avez la possibilit de le faire en toute quitude,  votre rythme.
            Commencez, interrompez-vous, reprenez plus tard. L'outil est souple, intuitif, regorge dinformations.
            Visitez-le  votre guise. A mesure que vous avancerez, votre rflexion sur vos dernires volonts pourra s'affiner.
            Et en plus, cet espace est entirement priv et scuris. Il vous est rserv.">
        <div className={style.main}>
          <div className={style.entries}>
            <h3 className={style.titleHome} >Les informations restant  complter</h3>
            <p className={style.introHome} > Un nombre important d'informations  personnelles ou administratives sont  renseigner dans cet espace. Pour faciliter sa compltude, cette rubrique restitue les informations qui ne sont pas encore renseignes.</p>
            {this.state.typeGroup.map((group, key) => {
              let docs = group.types
              if(docs && docs.length > 0) {
                console.log(docs);
                return (
                    <ul className={style.list}>
                      {docs.map((item, key) => (
                        item.hasOwnProperty("checked") ? <span></span>
                        :
                        <li  key={key}>
                          <Row id={item.slug} title={item.name} excerpt={item.excerpt}  check={item.checked} unique={true}
                               onAdd={e => {
                                 if( item.checked) {
                                   this.props.history.push(`/data/fcontacts/${item._id}`)
                                 } else {
                                   this.clickOnContacts(item);
                                 }
                               }}/>
                        </li>
                      ))}
                    </ul>
                )
              } else {
                return null
              }
            })}

            <div className={style.rowAd}>
              <div className={style.firstTitle}>
              </div>
              <div className={style.secondTitle}>
                Portail d'information Espace priv Dcs
              </div>
              <div className={style.Adtext}>
                Visitez notre site d'information pour anticiper au mieux vos obsques. <br />
                Dcouvrez Faire Face Au Dcs, le site qui met  votre disposition des informations autour du dcs, des obsques, du deuil...
              </div>
              <div className={style.Adlink}>
                <a href="http://maif.mymachinery.fr" target="_blank">EN SAVOIR +</a>
              </div>
            </div>
            <div className={style.separator}></div>
        </div>
        </div>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.open} closeOnEsc onClose={() => this.setState({open: false})}>
          <Modal>
            <DataForm
              closeModal={this.props.history.replace}
              slug={this.state.current}
              hiddenType={this.state.hiddenType}
              formType={this.state.formType}
              onboard={false}
              close={()=>{this.setState({open: false})}}
            />
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.openOnboarding} closeOnEsc onClose={() => this.setState({openOnboarding: false})}>
          <Modal>
            <DataForm
              closeModal={this.props.history.replace}
              step={this.steps}
              slug={this.state.current}
              onboard={true}
              hiddenType={this.state.hiddenType}
              close={()=>{this.setState({openOnboarding: false})}}
            />
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.openFirstOnboarding} closeOnEsc onClose={() => this.setState({openFirstOnboarding: false})}>
          <Modal>
            <div className={style.modal} onClick={e => e.stopPropagation()}>
              <img style={{width: 100+'%'}} src="media/onboardinginfo.jpg" alt=""/>
              <button onClick={() => this.closeFirstOnboarding()} className={classNames(button.button, button.default)}><i className="ion-ios-plus-empty"></i>SUIVANT</button>
            </div>
          </Modal>
        </PortalWithState>
        <PortalWithState closeOnOutsideClick={false} isOpened={this.state.InfoOpen} closeOnEsc onClose={() => this.setState({TypeOpen: false})}>
          <Modal>
            <div className={style.ModalMain}>
              <div className={style.edition} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                  <div>
                    <h1>{this.state.infoHeader}</h1>
                  </div>
                </div>
                <form className={style.content}>
                  <div className={style.row}>
                    {this.state.infoText}
                  </div>
                </form>
                <div className={style.footer}>
                  <div className={style.footerRight}>
                    <button
                      onClick={e => this.setState({ InfoOpen: false, infoText: ''})}
                      className={classNames(button.button, button.defaultLight)}
                    >
                      FERMER
                    </button>
                  </div>
                </div>
              </div>

            </div>
          </Modal>
        </PortalWithState>
      </Page>
    )

  }
}



